@charset "UTF-8";

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Light.woff2") format("woff2"), url("../font/Rubik-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Regular.woff2") format("woff2"), url("../font/Rubik-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Medium.woff2") format("woff2"), url("../font/Rubik-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Bold.woff2") format("woff2"), url("../font/Rubik-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote {
  margin: 0;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

textarea {
  resize: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a,
input,
button {
  outline: 0;
  border: 0;
}

button {
  background-color: transparent;
  cursor: pointer;
}

/*---------------------------------*/

::-webkit-scrollbar {
  width: 0;
}

*::-webkit-scrollbar {
  height: 2px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #ff6838;
  border-radius: 5px;
}

.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "Rubik", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: #31352b;
  background-color: #f9faff;
}

.logo {
  display: inline-block;
  border-radius: 5px;
  transition: opacity 0.3s, box-shadow 0.3s ease-in-out;
}

.logo:hover {
  opacity: 0.8;
}

.logo:focus {
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.logo:active {
  box-shadow: 0 0 0 2px transparent;
  opacity: 0.5;
}

.logo__img {
  width: 200px;
  height: 39px;
}

.link {
  border-radius: 5px;
  transition: color 0.3s, box-shadow 0.3s, text-shadow 0.3s ease-in-out;
}

.link:hover {
  color: #ff6838;
}

.link:focus {
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.link:active {
  color: #31352b;
  box-shadow: 0 0 0 2px transparent;
  text-shadow: 2px 2px 5px rgba(49, 54, 43, 0.5019607843);
}

.link--active {
  color: #ff6838;
}

.title {
  font-weight: 500;
  line-height: 1.3;
  font-size: 36px;
  text-align: center;
  color: #363853;
}

.title--span {
  display: block;
  text-align: left;
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: #fff;
  border-radius: 5px;
  background-color: #ff6838;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s ease-in-out;
}

.btn-bg:hover {
  color: #ff6838;
  background-color: rgba(255, 255, 255, 0.8901960784);
  box-shadow: 0 5px 12px rgba(255, 103, 56, 0.2), 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.btn-bg:focus {
  color: #ff6838;
  background-color: rgba(255, 122, 82, 0.1019607843);
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.btn-bg:active {
  color: #ff6838;
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px transparent;
}

.popular,
.restaurant,
.apps {
  padding: 120px 0;
}

.lock {
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease-in-out;
}

.lock .hero__link {
  z-index: -1;
}

.lock .filter-select {
  z-index: -1;
}

.lock .filter-number {
  z-index: -1;
}

/*---------------------------------*/

body.header-fixed .header {
  position: fixed;
  top: 0;
  right: 0;
  padding-top: 0;
  width: 100%;
  z-index: 1;
}

.header {
  position: fixed;
  padding-top: 57px;
  width: 100%;
  background-color: #f9faff;
  transition: 0.3s all ease-in-out;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar__logo {
  margin-left: 5px;
}

.navbar__inner {
  display: flex;
  align-items: center;
}

.menu {
  width: 301px;
  display: flex;
  justify-content: space-between;
  margin-right: 50px;
}

.menu__link {
  font-size: 18px;
  line-height: 1.1666666667;
}

.user {
  display: flex;
}

.user__item:nth-child(1) {
  margin-right: 10px;
}

.user__item:nth-child(2) {
  position: relative;
}

.user__btn {
  width: 44px;
  height: 44px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.user__btn:hover {
  background-color: #ff6838;
}

.user__btn:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px inset rgba(255, 104, 56, 0.7019607843);
}

.user__btn:active {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.user__btn:hover .user__icon {
  stroke: #fff;
}

.user__btn:hover .user__count {
  color: #ff6838;
  background-color: #fff;
}

.user__btn:focus .user__icon {
  stroke: #363853;
}

.user__btn:focus .user__count {
  color: #fff;
  background-color: #ff6838;
}

.user__icon {
  stroke: #363853;
  transition: stroke 0.3s ease-in-out;
}

.user__count {
  position: absolute;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  font-size: 10px;
  line-height: 1.2;
  color: #fff;
  background-color: #ff6838;
  top: 6px;
  right: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 0.3s, background-color 0.3s ease-in-out;
}

.burger {
  padding: 0;
  position: relative;
  width: 44px;
  height: 44px;
  border-radius: 5px;
  margin: 0 2px 0 10px;
  display: none;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.burger:hover {
  background-color: #ff6838;
}

.burger:hover .burger__line {
  background-color: #fff;
}

.burger:hover .burger__line::before,
.burger:hover .burger__line::after {
  background-color: #fff;
}

.burger:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px inset rgba(255, 104, 56, 0.7019607843);
}

.burger:focus .burger__line {
  background-color: #363853;
}

.burger:focus .burger__line::before,
.burger:focus .burger__line::after {
  background-color: #363853;
}

.burger:active {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.burger__line {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.burger__line::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  top: -10px;
  left: 0;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.burger__line::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  left: 0;
  bottom: -10px;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.burger-menu {
  display: flex;
  position: absolute;
  left: 0;
  top: 0;
  transform: translateX(-100%);
  transition: transform 0.3s ease-in-out;
}

.burger-menu__content {
  width: 300px;
  height: 100vh;
  background-color: #f9faff;
}

.burger-menu__top {
  padding: 25px 26px 25px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eeeff3;
}

.burger-menu__close {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  width: 20px;
  height: 20px;
  border-radius: 5px;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.burger-menu__close:hover {
  background-color: #ff6838;
}

.burger-menu__close:hover .burger-menu__icon {
  fill: #fff;
}

.burger-menu__close:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.burger-menu__close:focus .burger-menu__icon {
  fill: #363853;
}

.burger-menu__close:active {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.burger-menu__icon {
  width: 18px;
  height: 18px;
  fill: #363853;
  transition: fill 0.3s ease-in-out;
}

.burger-menu__bg {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5411764706);
}

.burger-menu--active {
  transform: translateX(0%);
}

.burger-menu .menu {
  display: block;
  border-bottom: 1px solid #eeeff3;
  padding: 25px 15px;
}

.burger-menu .menu__item:not(:last-child) {
  margin-bottom: 17px;
}

.burger-menu .contact {
  padding: 15px 0 0 15px;
}

.breadcrumbs {
  margin-top: 157px;
  background-color: #f9eded;
}

.breadcrumbs__list {
  padding: 26px 0;
  display: flex;
}

.breadcrumbs__item {
  padding-right: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.breadcrumbs__item:not(:last-child) {
  background-image: url(../img/svg/stack/sprite.svg#icon-chevron);
  background-repeat: no-repeat;
  background-position: right;
  background-size: 10px;
  margin-right: 6px;
}

.breadcrumbs__link {
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.01em;
  color: #505050;
}

.hero {
  padding-top: 160px;
}

.hero__inner {
  min-height: 660px;
  background-repeat: no-repeat;
  background-position: right;
  background-size: contain;
  display: flex;
  align-items: center;
}

.hero__title {
  font-size: 66px;
  font-weight: 500;
  line-height: 1.3;
  color: #363853;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.hero__title-span {
  display: block;
  color: #ff6838;
}

.hero__text {
  max-width: 450px;
  margin-bottom: 20px;
}

.hero__button {
  padding: 12px 31px;
  font-size: 18px;
  line-height: 1.3;
  letter-spacing: 0.03em;
  margin-right: 30px;
}

.hero__link {
  color: #363853;
  position: relative;
}

.hero__link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  left: 0;
  bottom: 0;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.hero__link:hover:after {
  background-color: transparent;
}

.hero__link:active {
  color: #363853;
}

