.contact__item {
  margin-bottom: 11px;
}

.contact__link {
  display: inline-flex;
  align-items: center;
  font-style: normal;
  font-size: 16px;
  line-height: 2;
}

.contact__link:hover .contact__icon {
  stroke: #ff6838;
}

.contact__link:active .contact__icon {
  filter: drop-shadow(2px 2px 5px rgba(49, 53, 43, 0.5019607843));
}

.contact__icon {
  stroke: #31352b;
  margin-right: 7px;
  transition: stroke 0.3s ease-in-out;
}

.social__list {
  display: flex;
}

.social__item:not(:last-child) {
  margin-right: 13px;
}

.social__link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 39px;
  height: 39px;
  border-radius: 50%;
  background-color: #ececec;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.social__link:hover {
  background-color: #ff6838;
}

.social__link:hover .social__icon {
  background-color: #fff;
}

.social__link:focus {
  background-color: #fff2ed;
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.social__link:focus .social__icon {
  background-color: #31352b;
}

.social__link:active {
  background-color: #fff2ed;
  box-shadow: 0px 0px 0px 2px transparent;
}

.social__link:active .social__icon {
  background-color: #ff6838;
}

.social__icon {
  display: inline-block;
  background-color: #31352b;
  mask-size: cover;
  -webkit-mask-size: cover;
  mask-position: center;
  -webkit-mask-position: center;
  mask-repeat: no-repeat;
  -webkit-mask-repeat: no-repeat;
  transition: background-color 0.3s ease-in-out;
}

@media (max-width: 1130px) {
  .catalog__filter-btn {
    display: inline-flex;
  }

  .catalog__inner {
    justify-content: center;
  }

  .catalog__filters {
    margin-right: 0;
  }

  .filter {
    position: absolute;
    top: 0;
    left: 0;
    padding: 80px 0;
    width: 320px;
    background-color: #fff;
    transform: translateX(-100%);
  }

  .filter__btn {
    display: inline-flex;
  }

  .popular-food {
    grid-template-columns: repeat(4, 1fr);
  }

  .restaurant__list {
    grid-template-columns: repeat(2, 1fr);
  }

  .apps__inner {
    background-size: cover;
    box-shadow: inset 0 0 0 200vw rgba(249, 250, 255, 0.768627451);
    justify-content: center;
  }

  .discount__title {
    text-align: center;
  }

  .discount__list {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }

  .footer__top {
    grid-template-columns: 25.33% 20.33% 22.33%;
    grid-row-gap: 30px;
    grid-template-areas: "form address address" "list-one list-two list-three";
