  }

  .popular,
  .restaurant,
  .apps {
    padding: 90px 0;
  }

  .hero__inner {
    min-height: 300px;
  }

  .hero__title {
    font-size: calc(40px + 44.2 * ((100vw - 375px) / 1200));
  }

  .catalog-food {
    grid-template-columns: repeat(3, 1fr);
  }

  .popular-food {
    grid-template-columns: repeat(3, 1fr);
  }

  .delivery__inner {
