  .delivery__content {
    padding-top: 28px;
    max-width: 348px;
    margin-right: 0;
  }

  .delivery__item {
    font-size: calc(18px + 10.2 * ((100vw - 375px) / 1200));
  }

  .restaurant__item {
    display: flex !important;
    justify-content: center;
  }

  .restaurant .slick-list {
    min-height: 449px;
  }

  .apps__text {
    font-size: calc(14px + 10.2 * ((100vw - 375px) / 1200));
  }

  .discount {
